
header{width: 100%;height: 1.5rem;z-index: 9;padding-left: 5%;position: fixed;background-image: -webkit-linear-gradient(bottom, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.51) 95%);background-image: linear-gradient(0deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.51) 95%);
}
header .iconfont{color: #fff;font-size: 0.8rem;top: 20%;position: relative;
}
.aside{z-index: 11;left: 0;right: 0;top: 0;bottom: 0;position: fixed;visibility: hidden;
}
.aside ul{margin: 0;float: left;width: 60%;height: 100%;padding: 1.3rem 0.5rem 0.5rem;overflow: auto;background: rgba(91, 116, 146, 0.75);-webkit-transform: translate(-100%, 0);transform: translate(-100%, 0);-webkit-transition: -webkit-transform 0.3s ease;transition: -webkit-transform 0.3s ease;transition: transform 0.3s ease;transition: transform 0.3s ease, -webkit-transform 0.3s ease;-webkit-overflow-scrolling: touch;
}
.aside.docked{visibility: visible;
}
.aside.open ul{-webkit-transform: translate(0);transform: translate(0);
}
.aside ul li{cursor: pointer;font-size: 0.43rem;list-style: none;color: #fff;margin-top: 20px;
}
.aside ul li .iconfont{float: right;font-size: 0.6rem;
}
.aside ul li.select{color: #FFD300;
}
.app-view{z-index: 1;width: 100vw;height: 100vh;overflow: auto;position: absolute;-webkit-transition: -webkit-transform 0.3s ease;transition: -webkit-transform 0.3s ease;transition: transform 0.3s ease;transition: transform 0.3s ease, -webkit-transform 0.3s ease;-webkit-overflow-scrolling: touch;
}



