
.init-scroll{display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;align-items: center;padding-bottom: 8px;line-height: 36px;width: 100%;
}
.init-scroll-text {margin-left: 16px;font-size: 16px;
}
.loader{width: 30px;height: 30px;position: relative;
}
.loader-path{stroke-dasharray: 1, 200;stroke-dashoffset: 0;-webkit-animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;stroke-linecap: round;
}
@-webkit-keyframes dash{
0%{stroke-dasharray: 1, 200;stroke-dashoffset: 0;
}
50%{stroke-dasharray: 89, 200;stroke-dashoffset: -35;
}
100%{stroke-dasharray: 89, 200;stroke-dashoffset: -124;
}
}
@keyframes dash{
0%{stroke-dasharray: 1, 200;stroke-dashoffset: 0;
}
50%{stroke-dasharray: 89, 200;stroke-dashoffset: -35;
}
100%{stroke-dasharray: 89, 200;stroke-dashoffset: -124;
}
}
.loader-circular{-webkit-animation: rotate 2s linear infinite;animation: rotate 2s linear infinite;height: 100%;-webkit-transform-origin: center center;transform-origin: center center;width: 100%;position: absolute;top: 0;bottom: 0;left: 0;right: 0;margin: auto;
}
@-webkit-keyframes rotate {
from{-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
50%{-webkit-transform: rotate(180deg);transform: rotate(180deg);
}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg);
}
}
@keyframes rotate {
from{-webkit-transform: rotate(0deg);transform: rotate(0deg);
}
50%{-webkit-transform: rotate(180deg);transform: rotate(180deg);
}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg);
}
}
@-webkit-keyframes color {
0%,100%{stroke: #FFD300;
}
40%{stroke: #5B7492;
}
66%{stroke: #FFD300;
}
80%,90%{stroke: #acb9c8;
}
}
@keyframes color {
0%,100%{stroke: #FFD300;
}
40%{stroke: #5B7492;
}
66%{stroke: #FFD300;
}
80%,90%{stroke: #acb9c8;
}
}



