
img{width: 100%;
}
.app-view .swiper-container{width:100%;
}
.app-view .swiper-slide{height: 8rem;overflow: hidden;position: relative;
}
.app-view .swiper-container-horizontal .swiper-pagination-bullets{bottom: 1rem;width: 95%;text-align: right;
}
.app-view .newsList:nth-child(2){margin-bottom: 0;padding-top: 0;
}
.app-view .newsList:nth-child(2) .newsList-time{top: -.8rem;
}
.swiper-slide div{top: 0;left: 0;width: 100%;height: 100%; opacity: 0.4;position: absolute;
}
.swiper-slide img{top: 50%;position: relative;-webkit-transform: translate(0, -50%);transform: translate(0, -50%);
}
.swiper-slide h3{width: 70%;color: #fff;margin: 0;font-size: 0.5rem;line-height: 1rem;right: 5%;bottom: 2.6rem;text-align: right;position: absolute;text-shadow: 1px 1px 10px rgba(0, 0, 0, .5);
}
.newsList{width: 90%;z-index: 1;position: relative;padding-top: 0.8rem;margin: .8rem auto 0;
}
.newsList .newsList-time{top: 0;margin: 0;color: #fff;padding: 0 1rem;font-size: 0.4rem;line-height: 0.8rem;letter-spacing: 0.1rem;border-radius: 0.4rem;text-align: center;background-color:yellow;-webkit-transform: translate(0,-50%);transform: translate(0,-50%);position: absolute;box-shadow: 0 3px 10px 0 rgba(91,115,146,0.15);z-index:2;
}
.newsList .newsList-con{cursor: pointer;display: -webkit-box;display: -ms-flexbox;display: flex;padding: 0.3rem;margin-bottom: 0.4rem;border-radius: 0.15rem;-webkit-box-align: center;-ms-flex-align: center;align-items: center;background-color: #fff;box-shadow: 0 3px 10px 0 rgba(91,115,146,0.15);
}
.newsList .newsList-con img{width: 2rem;margin-right: 0.4rem;
}
.newsList .newsList-con p{color:#5B7492;font-size: 0.4rem;text-align: justify;margin: 0;
}
.loading{width: 25%;height: 0.4rem;margin: 25% auto 0;position: relative;
}
.loading span{display: block;width: 0.4rem;height: 0.4rem;border-radius: 50%;position: absolute;background: @blue;-webkit-transform: translate(-50%,0);transform: translate(-50%,0);
}
.loading .left{background:yellow;-webkit-animation: lLeft 2.5s ease infinite;animation: lLeft 2.5s ease infinite;
}
@-webkit-keyframes lLeft{
0%{left: 0;
}
25%{left: 50%;background: yellow;
}
50%{left: 100%;background: blue;
}
75%{left: 50%;background: gray;
}
100%{left: 0;
}
}
@keyframes lLeft{
0%{left: 0;
}
25%{left: 50%;background: yellow;
}
50%{left: 100%;background: blue;
}
75%{left: 50%;background: gray;
}
100%{left: 0;
}
}



